<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="orderList" border stripe style="width: 100%">
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column label="">
          <template #default="props">
            <el-tag v-if="props.row.pay_status==='0'" type="danger"></el-tag>
            <el-tag v-else type="primary"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="is_send" label="">
          <template
            #default="props"
          >{{new Date(props.row.create_time).toLocaleDateString()}} {{new Date(props.row.create_time).toLocaleTimeString()}}</template>
        </el-table-column>
        <el-table-column prop="is_send" label="">
          <template #default="props">
            <el-button size="mini" type="warning" icon="el-icon-edit" @click="showEditDialog"></el-button>
            <el-button
              size="mini"
              type="success"
              icon="el-icon-location"
              @click="showProgressDialog(props.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <el-dialog title="" :visible.sync="editVisible" width="50%" @close="handleClose">
        <el-form :model="eidtForm" :rules="eidtFormRules" ref="eidtFormRef" label-width="100px">
          <el-form-item label="//" prop="address1">
            <el-cascader v-model="eidtForm.address1" :options="cityData" :props="cascaderProps"></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="eidtForm.address2"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editVisible = false"> </el-button>
          <el-button type="primary" @click="editVisible = false"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="progressVisible" width="50%">
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.time"
          >{{activity.context}}</el-timeline-item>
        </el-timeline>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import cityData from './city-picker-app';
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      // 
      orderList: [],
      editVisible: false,
      // 
      eidtForm: {
        // 
        address1: [],
        // 
        address2: ''
      },
      eidtFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'name',
        label: 'name',
        children: 'sub'
      },
      // 
      progressVisible: false,
      // 
      progressInfo: []
    };
  },
  mounted() {
    this.getOderList();
  },
  methods: {
    async getOderList() {
      const { data: res } = await this.$axios.get('orders', {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) return this.$message.error('');
      this.total = res.data.total;
      this.orderList = res.data.goods;
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getOderList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getOderList();
    },
    // 
    handleClose() {
      this.$refs.eidtFormRef.resetFields();
    },
    // 
    showEditDialog() {
      this.editVisible = true;
    },
    // 
    async showProgressDialog(row) {
      this.progressVisible = true;
      const { data: res } = await this.$axios.get('/kuaidi/1106975712662');
      if (res.meta.status !== 200) { return this.$message.error('!'); }
      this.progressInfo = res.data;
    }
  }
};
</script>

<style lang='stylus' scoped>
.el-table
  margin-top: 10px
</style>
